import {ReactChild, FC, ReactNode, Ref} from 'react'
import { Box, useTheme } from '@mui/material'
import styled from 'styled-components'

const { log } = console

interface TextProps {

    children: ReactChild | ReactNode,
    className?: String,
    onClick?: (event: any) => void,
    ref?: Ref<any>,

}


const Text: FC<TextProps> = ({ children, className, onClick, ref }) => {

    const theme = useTheme()

    const Styles = styled.p`
        color: ${theme.palette.text.primary};
        font-family: Rubik;
        font-size: 14px;
        font-style: normal;
        font-weight: 300;
        line-height: 22px; /* 157.143% */
    `

    return (
        <Box ref={ref} component={Styles} onClick={onClick} className={`Text ${className}`}>
            {children}
        </Box>
    )
}
export default Text